
import tab from '../components/footerTab/tabbar'
import musicplayer from '../components/musicPlayer/index'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import {loginStatus} from '../http/req'
export default {
directives: {
TransferDom
},
data() {
return {
getMusic: false,
}
},
components:{
tab,musicplayer,Popup
},
methods: {
routerLink(index) {
switch (index) {
case 0:
this.$router.push({
path: 'discover'
});
break;
default:
}
},
getLoginStatus(){
loginStatus().then(res=>{
localStorage.setItem('userId',res.data.profile.userId)
console.log(res.data.profile.userId);
}).catch(err=>{

})
},
openMusicPlayer(){
this.$store.dispatch("toOpenMusic")
},
closeMusicPlayer(){
this.$store.dispatch("toCloseMusic")
}
},
created(){
this.getLoginStatus();
},
computed:{
isMusic:{
get() {
return this.$store.state.openMusic
},
set(value) {          // 使用vuex中的mutations中定义好的方法来改变
this.$store.state.openMusic = value
}
},
openMusic(){

},
closeMusic(){

}
}
}



