
import {
recommendSongs
} from '../../http/req'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
data() {
return {
songsList: [],
select:true,
openMethod:true,
isLoading:true
}
},
components: {
Flexbox,
FlexboxItem,
Divider,
Swipeout,
SwipeoutItem,
SwipeoutButton,
LoadMore
},
methods: {

getRecommendSongs() {
recommendSongs().then(res => {
if (res.status == 200) {
console.log(res.data.recommend);
this.songsList = res.data.recommend
this.isLoading = false
}
}).catch(err => {
console.log(err);
})
},
openMusic(index){
this.$store.dispatch("giveSong",this.songsList)
this.$store.dispatch("toOpenMusic")
this.$store.commit("GETSONGINDEX",index);
this.$store.dispatch("playMusic")
},
none(){

},
onButtonClick (type) {
console.log('ok');
this.openMethod = false
},
eventOpen () {
this.select = false
},
eventClose(){
this.select = true
this.openMethod = true
}
},
created() {
this.getRecommendSongs();
}
}



