const mutations = {
    login(state) {   //自定义改变state初始值的方法，这里面的参数除了state之外还可以再传额外的参数(变量或对象);
        state.loginStatus = true;
    },
    logout(state){
      state.loginStatus = false;
    },
    OPENMUSIC(state) {  //同上
        state.openMusic = true;
    },
    CLOSEMUSIC(state) {  //同上
        state.openMusic = false;
    },

    // 播放器
    GETSONG(state,val){
      state.songList = val

    },
    GETSONGINDEX(state,val){
      state.songIndex = val
    },
    ISPLAYING(state){
      state.isPlaying = true
    },
    NOTPLAYING(state){
      state.isPlaying = false
    },

    // 获取歌单id
    GETSONGSHEETID(state,val){
      state.songSheetId = val
    },
    // 路由切换显示加载中
    updateLoadingStatus (state, payload) {
      state.isLoading = payload.isLoading
    }
}
export default mutations



