
import {
playListDetail
} from '../../http/req'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
directives: {
TransferDom
},
data() {
return {
songsList: [],
songSheetDetail:{},
select:true,
openMethod:true,
isLoading:true,
songNum:20
}
},
components: {
Flexbox,
FlexboxItem,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Loading,
ViewBox
},
methods: {

getplayListDetail() {
playListDetail({id:this.getId}).then(res => {
if (res.status == 200) {
this.songSheetDetail = res.data.playlist
this.songsList = res.data.playlist.tracks
this.isLoading = false
}
}).catch(err => {
console.log(err);
})
},
openMusic(index){
this.$store.dispatch("giveSong",this.songsList)
this.$store.dispatch("toOpenMusic")
this.$store.commit("GETSONGINDEX",index);
this.$store.dispatch("playMusic")
},
none(){

},
onButtonClick (type) {
console.log('ok');
this.openMethod = false
},
eventOpen () {
this.select = false
},
eventClose(){
this.select = true
this.openMethod = true
},
getScroll(){
if (((window.screen.height + document.body.scrollTop) > (document.body.clientHeight))) {
this.songNum +=20

}
}
},
computed:{
getId: {
get() {
return this.$store.state.songSheetId
},
set: function(id) {
this.$store.state.songSheetId = id
}
},
},
created() {
this.getplayListDetail();

},
mounted () {

window.addEventListener('scroll', this.scrollBottom)
},

}



