/**
 * 此页面用于封装所有api请求地址
 */
class AllApi {
    constructor(){
        this.newsMv = '/mv/first'//最新mv请求
        this.tuijianMv = '/personalized/mv'//推荐mv请求
        this.banner ='/banner'
        this.recommend = '/recommend/resource'
        // 手机登录接口
        this.phoneLogin = '/login/cellphone'
        this.ctcode = '/captch/sent'
        this.captcha = '/captch/verify'
        // 邮箱登录
        this.emailLogin = '/login'
        this.personalFm = '/personal_fm'
        this.loginStatus= '/login/status'


        this.likelist = '/likelist'
        this.recommendSongs = 'recommend/songs'

        // 获取歌曲
        this.getSong = '/song/url'
        // 搜索歌曲
        this.searchSong = '/search'
        // 获取歌曲详情
        this.songDetail = '/song/detail'
        // 获取用户歌单
        this.playlist = '/user/playlist'
        // 获取歌单详情
        this.playListDetail = 'playlist/detail'

    }
}

export const Api = new AllApi()



