/**
 * 此页面用于所有请求
 */
import {request} from './http'
import {Api} from './api'
export const banner = data => request(Api.banner, data || {}, 'get')
export const recommend = data => request(Api.recommend, data || {}, 'get')
// 手机登录接口
export const phoneLogin = data => request(Api.phoneLogin, data || {}, 'get')
export const ctcode = data => request(Api.ctcode, data || {}, 'get')
export const captcha = data => request(Api.captcha, data || {}, 'get')
// 邮箱登录接口
export const emailLogin = data => request(Api.emailLogin, data || {}, 'get')
export const loginStatus = data => request(Api.loginStatus, data || {}, 'get')




export const personalFm = data => request(Api.personalFm, data || {}, 'get')

export const likelist = data => request(Api.likelist, data || {}, 'get')
// 每日推荐歌曲
export const recommendSongs = data => request(Api.recommendSongs, data || {}, 'get')

// 获取歌曲
export const getSong = data => request(Api.getSong, data || {}, 'get')
// 获取搜索歌曲
export const searchSong = data => request(Api.searchSong, data || {}, 'get')
// 获取歌曲详情
export const songDetail = data => request(Api.songDetail, data || {}, 'get')
// 获取用户歌单
export const playlist = data => request(Api.playlist, data || {}, 'get')
// 获取歌单详情
export const playListDetail = data => request(Api.playListDetail, data || {}, 'get')


export const newsMv = data => request(Api.newsMv, data || {}, 'get')

export const tuijianMv = data => request(Api.tuijianMv, data || {}, 'post')



