const action = {
   changeLogin(context) {  //自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性
       context.commit('login');
   },
   toLogout(context){
     context.commit('logout');
   },


   toOpenMusic(context) {  //同上注释
       context.commit('OPENMUSIC');
   },
   giveSong(context,val){
     context.commit('GETSONG',val);
   },
   stopMusic(context){
     context.commit('NOTPLAYING');
   },
   playMusic(context){
     context.commit('ISPLAYING');
   },
   toCloseMusic(context) {  //同上注释
       context.commit('CLOSEMUSIC');
   },

   getSongSheetId(context,val){
     context.commit('GETSONGSHEETID',val);
   }
};
export default action



