
import {
banner,
recommend,
recommendSongs,
searchSong,
playListDetail
} from '../../http/req'
import selectItem from '../../components/discover/selectItem'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
directives: {
TransferDom
},
data() {
return {
searchVal: '',
openSearch:false,
searchSongList:[],
bannerList: [],
recommendList:[],
demo06_index: 0,
selectItem: [{
icon: require('../../assets/icon/date.png'),
name: '每日推荐'
}, {
icon: require('../../assets/icon/songs.png'),
name: '歌单'
}, {
icon: require('../../assets/icon/paihang.png'),
name: '排行榜'
}, {
icon: require('../../assets/icon/channel.png'),
name: '电台'
}, {
icon: require('../../assets/icon/online.png'),
name: '直播'
}]
}
},
components: {
Swiper,
SwiperItem,
Popup,
Group,
Cell,
Flexbox, FlexboxItem
},
methods: {
toOpenSearch(){
this.openSearch =true
},
openMusic() {
this.$store.dispatch("toOpenMusic")
},
searchSong(){
searchSong({keywords:this.searchVal}).then(res=>{
if(res.status == 200){
this.searchSongList = res.data.result.songs
console.log(this.searchSongList);
}

}).catch(err=>{
console.log(err);
})
},
openSearchMusic(index){
this.openSearch =false
this.$store.dispatch("giveSong",this.searchSongList)
this.$store.dispatch("toOpenMusic")
this.$store.commit("GETSONGINDEX",index);
this.$store.dispatch("playMusic")
},

demo06_onIndexChange(index) {
this.demo06_index = index
},
getBaner() {
banner().then(res => {
let data = res.data
let list = []
let list2 = []
if (data.code == 200) {
for (let i in data) {
list.push(data[i])
}
list.forEach((item, index) => {
if (index == 0) {
for (var i = 0; i < item.length; i++) {
list2.push({
img: item[i].imageUrl,
url: item[i].imageUrl,
title: item[i].typeTitle
})
}
}
})

this.bannerList = list2.map((item, index) => ({
url: item.url,
img: item.img,
title: item.title
}))
}
}).catch(err => {
console.log(err);
});
},

getRecommend() {
recommend().then(res => {
this.recommendList = res.data.recommend
}).catch(err => {
console.log(err);
})
},
goSongSheet(id){
this.$router.push({
name: 'songSheet'
})
this.$store.dispatch("getSongSheetId",id)
},







routerLink(index) {
switch (index) {
case 0:
this.$router.push({
name: 'recommendSongs'
})
break;
default:

}
}
},
created() {
this.getBaner();
this.getRecommend();
const urlList = this.bannerList.map((item, index) => ({
url: item.url,
img: item.img,
title: `(可点击)${item.title}`
}))
},
computed: {
isLogin() {
return this.$store.getters.isLogin;
}
}

}



