
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'


import {
personalFm,
likelist,
playlist
} from '../http/req'
let userId = localStorage.getItem('userId');
export default {
components: {
Group,
Cell,

},
data() {
return {
userPlayList: [],
topList: [{
icon: require('../assets/myMusic/personFm.png'),
name: '私人FM'
}, {
icon: require('../assets/myMusic/dianyin.png'),
name: '最新电音'
}, {
icon:require('../assets/myMusic/moon.png'),
name: 'Sati空间'
}, {
icon:require('../assets/myMusic/recommendCollect.png'),
name: '私藏推荐'
}, {
icon:require('../assets/myMusic/child.png'),
name: '亲子频道'
}, {
icon:require('../assets/myMusic/piano.png'),
name: '古典专区'
},
{
icon:require('../assets/myMusic/run.png'),
name: '跑步FM'
},
{
icon:require('../assets/myMusic/sax.png'),
name: '爵士电台'
},
{
icon:require('../assets/myMusic/car.png'),
name: '驾驶模式'
},]
}
},
methods: {
getPersonalFm() {
personalFm().then(res => {
console.log(res);
}).catch(err => {
console.log(err);
})
},
getLikeList() {
likelist({
uid: userId
}).then(res => {

}).catch(err => {

})
},
getPlayList() {
playlist({
uid: userId
}).then(res => {
if (res.status == 200) {
this.userPlayList = res.data.playlist
console.log(this.userPlayList);
}

}).catch(err => {
console.log(err);
})
},
getSongSheetId(index){
let getId = this.userPlayList[index].id
this.$store.dispatch("getSongSheetId",getId)
this.$router.push({
name: 'songSheet'
})
}

},
created() {
this.getLikeList();
this.getPlayList();
}
}



