
import Range from 'vux/src/components/range/index.vue'

import {
getSong,songDetail
} from '../../http/req'
export default {
data() {
return {
getData: false,
title: '歌名',
name: '人名',
changeSong:0,
time: 0,
songIndex:0,

songURL: '',
songDetail:[],

unknowSonger:'this.src="' + require('../../assets/musicPlayer/unknowSonger.png') + '"',
imgErr:false,
currentTime:0,
endTime:0,
totalTime:0,

audio: {
playing: false
},
selectList: [require('../../assets/musicPlayer/like.png'), require('../../assets/musicPlayer/download.png'), require('../../assets/musicPlayer/comment.png'), require('../../assets/musicPlayer/select.png')],
playList: [require('../../assets/musicPlayer/prev.png'), require('../../assets/musicPlayer/play.png'), require('../../assets/musicPlayer/next.png')]
}
},
components: {
Range
},
methods: {
changeImg(){
this.imgErr =true
},
updateTime(e) {
this.currentTime = e.target.currentTime
this.totalTime = e.target.duration
this.endTime = e.target.ended
},
touchChange(){},
format(interval) {
interval = interval | 0
const minute = interval / 60 | 0
const second = this._pad(interval % 60)
return `${minute}:${second}`
},
_pad(num,n=2){
let len = num.toString().length
while (len<n) {
num = "0"+num
len++
}
return num
},
onChange(val) {

},
closeMusicPlayer() {
this.$store.dispatch("toCloseMusic")
},
getSongURL() {
getSong({
id: this.songID
}).then(res => {
if (res.status == 200) {
let data = res.data.data
for (var i = 0; i < data.length; i++) {
this.songURL = data[0].url;
}
}

}).catch(err => {
console.log(err);
})
},
getSongDetail(){
songDetail({ids:this.songID}).then(res => {
if (res.status == 200) {
this.songDetail = res.data.songs[0]
}

}).catch(err => {
console.log(err);
})
},
stopMusic() {
this.$store.dispatch("stopMusic")
this.pause()
},
play() {
this.$store.dispatch("playMusic")
this.$refs.audioPlay.play()
},

pause() {
this.$refs.audioPlay.pause()
},

prev(){
this.changeSong = 1
let songIndex  = this.getSongIndex
let max = this.MaxIndex()
if(songIndex === 0){
songIndex = max
this.$store.commit("GETSONGINDEX",songIndex);
}
else {
songIndex -= 1
this.$store.commit("GETSONGINDEX",songIndex);
}

},

next(){
this.changeSong = 2
let songIndex  = this.getSongIndex
let max = this.MaxIndex()
if(songIndex === max){
songIndex = 0
this.$store.commit("GETSONGINDEX",songIndex);
}
else {
songIndex+=1
this.$store.commit("GETSONGINDEX",songIndex);
}

},
MaxIndex(){
let list = this.getSong
let max = 0
for (var i = 0; i < list.length; i++) {
if(max<i){
max = i
}
}
return max;
},
},
computed: {

getSong: {
get() {
return this.$store.state.songList
},
set: function(list) {
this.$store.state.songList = list
}
},

getSongIndex: {
get() {
return this.$store.state.songIndex
},
set: function(list) {
this.$store.state.songIndex = list
}
},

getSongList() {
return this.getSong[this.getSongIndex];
},

songID(){
return this.getSong[this.getSongIndex].id;
},

isPlaying: {
get() {
return this.$store.state.isPlaying
},
set: function(val) {
this.$store.state.isPlaying = val
}
},
},

watch:{

getSongList:function(newval,oldval){
this.getSongURL()
this.getSongDetail()
},
endTime:function(newval,oldval){
if(newval == true){
this.next();
this.$store.dispatch("stopMusic");
}
},
getSong:function(newval,oldval){
return newval
}
},
created(){
}

}



