import {phoneLogin,ctcode} from '../../http/req'
export default {
  data(){
    return{
      clearable: {
        visible: true,
        blurHidden: true
      },
      value: '',
      placeholder: '请输入内容',
      type: 'password',
      maxlength: 16,
      autofocus: true,
      autocomplete: true,
      eye: {
        open: false,
        reverse: false
      },
      phone:'',
      password:'',
      code:''
    }
  },
  methods:{
    phoneLogin(){
      phoneLogin({phone:this.phone,password:this.password}).then(res=>{
        console.log(res.data);
      }).catch(err=>{
        console.log(err);
      })
    }
  }
}



