/**
 * 此页面用于封装get post update方法
 */
import {service} from '../utlis/index'
import qs from 'qs'

/**
 * 导出request 用于外面调用

 */
export const request =  (url, params, methods) => {
    let method = methods.toLocaleUpperCase()
    if (method == 'GET') {
        return new Promise((resolve, reject) => {
            service.get(url, {params:params})
                .then(res=>{
                    resolve(res)
                })
                .catch(err=>{
                    console.log('err:', err)
                    reject(err)
                })
        })
    } else if (method == 'POST') {
        return new Promise((resolve, reject) => {
            service.post(url, params)
                .then(res=>{
                    resolve(res)
                })
                .catch(err=>{
                    reject(err)
                })
        })
    } else {
        service.headers = {
            'Content-Type': 'multipart/form-data'
        }
        return new Promise((resolve, reject) => {
            service.post(url, params)
                .then(res=>{
                    resolve(res)
                })
                .catch(err=>{
                    reject(err)
                })
        })
    }
}



