
import {
ctcode,
captcha
} from '../../http/req'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data() {
return {
phoneNum: '',
showConfirm: false,
code: '',
codeName: '获取验证码',
errMessage: false,
totalTime:60,
canClick: false
}
},
components: {
XHeader
},
methods: {
getCtcode() {
if(this.canClick == true){
ctcode({
phone: this.phoneNum
}).then(res => {
if (res.status == 200) {

this.countDown();
}
}).catch(err => {
console.log(err);
})
}

},
login() {
if (this.phoneNum != '' && this.code != '') {
captcha({
phone: this.phoneNum,
captcha: this.code
}).then(res => {
if (res.status == 200) {
console.log(res);
this.getLogin();
let loginStatus = true;
localStorage.setItem('loginStatus', loginStatus)
this.$router.push({
path: 'index/discover'
})
} else if (res.status == 503) {
this.showToastTxtOnly();
}
}).catch(err => {
console.log(err);
})
}
else {

}
},
getLogin() {
this.$store.commit('login')
},
showToastTxtOnly() {
this.toast = this.$createToast({
txt: '验证码错误',
type: 'txt'
})
this.toast.show()
},
countDown(){
if (!this.canClick) return  
this.canClick = false
this.codeName = this.totalTime + 's后重新发送'
let clock = window.setInterval(() => {
this.totalTime--
this.codeName = this.totalTime + 's后重新发送'
if (this.totalTime < 0) {
window.clearInterval(clock)
this.codeName = '重新发送验证码'
this.totalTime = 10
this.canClick = true  
}
},1000)
}
},
watch: {
phoneNum:function (newval,oldval){
let phone = /^1[345789][0-9]{9}$/
if (newval != '' && phone.test(newval) == true) {
this.canClick = true
} else {
this.canClick = false
}
},
code:function(newval,oldval) {
if (newval.length > 4) {
this.errMessage = true
} else {
this.errMessage = false
}
}
}

}



